<template>
  <div>
    <div
      class="card out-card"
      :class="`bg-${color || 'primary'}`"
      @click="$emit('click')"
      ref="card"
    >
      <div v-if="!type || type === 'normal'" class="card">
        <div class="card-body p-3 d-flex align-items-center">
          <span
            :class="`b-avatar badge-light-${color || 'primary'} rounded-circle`"
            style="width: 60px; height: 60px"
          >
            <span class="b-avatar-custom">
              <slot name="icon"></slot>
            </span>
          </span>
          <div class="truncate ms-3 appcard-textarea">
            <div class="d-flex align-items-center flex-wrap">
              <slot name="title"></slot>
            </div>
            <div class="d-none d-sm-block">
              <slot name="description"></slot>
            </div>
          </div>
          <div class="appcard-btnarea">
            <slot name="btnarea"></slot>
          </div>
        </div>
      </div>
      <div v-if="type === 'box'" class="card text-center">
        <div class="card-body">
          <span
            class="b-avatar mb-3 rounded-circle"
            :class="`badge-light-${color || 'primary'}`"
            style="width: 60px; height: 60px"
          >
            <span class="b-avatar-custom">
              <slot name="icon"></slot>
            </span>
          </span>
          <div class="truncate">
            <slot name="title"></slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Card",
  props: ["color", "top", "left", "right", "bottom", "type"],
  mounted() {
    if (this.top !== undefined) {
      this.$refs.card.style.paddingTop = this.top || ".25rem";
    } else if (this.left !== undefined) {
      this.$refs.card.style.paddingLeft = this.left || ".25rem";
    } else if (this.right !== undefined) {
      this.$refs.card.style.paddingRight = this.right || ".25rem";
    } else if (this.bottom !== undefined) {
      this.$refs.card.style.paddingBottom = this.bottom || ".25rem";
    }

    if (
      this.top === undefined &&
      this.left === undefined &&
      this.right === undefined &&
      this.bottom === undefined
    ) {
      this.$refs.card.style.paddingTop = ".25rem";
    }
  },
};
</script>

<style lang="less" scoped>
.out-card {
  display: inline-block;
  padding: 0;
  // padding-top: .25rem;
  box-shadow: none;
  cursor: pointer;
  width: 100%;

  .card {
    box-shadow: 0 4px 24px 0 rgba(34, 41, 47, 0.1);
    background: #fff;
    opacity: 1;
  }
}

.appcard-textarea {
  flex-grow: 1;
}
.appcard-btnarea {
  text-align: right;
}

@media screen and (max-width: 400px) {
  .b-avatar {
    display: none !important;
  }
}
</style>
